import HeaderBack from '../Common/HeaderBack'
export default {
  name: 'useBox',
  components: {
    myHeader: HeaderBack,
  },
  data () {
    return {
      header: {
        HBL_icon:"icon_back", 
        HBC_text:"盒子激活", 
        HBR_icon: " ",
      },
      boxNum: '',
      actSure: false,
    }
  },
  methods: {
    actBtnFn() {
      this.actSure = true;
      alert("确定激活")
    }
  }
}



